import React, { Component } from "react";
import NewsItem from "./NewsItem";
import { Button, Spinner } from "react-bootstrap";

export class News extends Component {
  articles = [];
  constructor() {
    super();
    this.state = {
      articles: this.articles,
      loading: false,
      page: 1,
      totalArticles: 0,
    };
  }
  async componentDidMount() {
    let url = `https://newsapi.org/v2/top-headlines?country=in&apiKey=03dba794f9a944fdafe029e17741bcee&page=1&pageSize=${this.props.pagesize}`;
    let data = await fetch(url);
    this.setState({ loading: true });
    let parsedData = await data.json();
    this.setState({
      articles: parsedData.articles,
      totalArticles: parsedData.totalResults,
      loading: false,
    });
  }
  handleNextClick = async () => {
    if (
      this.state.page + 1 <
      Math.ceil(this.state.totalArticles / this.props.pagesize)
    ) {
      let url = `https://newsapi.org/v2/top-headlines?country=in&apiKey=03dba794f9a944fdafe029e17741bcee&pageSize=${
        this.props.pagesize
      }&page=${this.state.page + 1}`;
      this.setState({ loading: true });
      let data = await fetch(url);
      let parsedData = await data.json();
      this.setState({
        page: this.state.page + 1,
        articles: parsedData.articles,
        loading: false,
      });
    }
  };
  handlePreviousClick = async () => {
    let url = `https://newsapi.org/v2/top-headlines?country=in&apiKey=03dba794f9a944fdafe029e17741bcee&pageSize=${
      this.props.pagesize
    }&page=${this.state.page - 1}`;
    let data = await fetch(url);
    let parsedData = await data.json();
    this.setState({ page: this.state.page - 1, articles: parsedData.articles });
  };
  render() {
    return (
      <div>
        <div className="container my-3">
          <h1 className="text-center">News Monkey Top Headlines.</h1>
          {this.state.loading && (
            <Spinner animation="border" role="status">
              <span className="visually-hidden"> Loading...</span>
            </Spinner>
          )}
          <div className="row">
            {this.state.articles.map((element) => {
              return (
                <div className="col-md-4 my-2">
                  <NewsItem
                    key={element.url}
                    title={element.title ? element.title : ""}
                    description={element.description ? element.description : ""}
                    image_url={
                      element.urlToImage
                        ? element.urlToImage
                        : "https://images.cnbctv18.com/wp-content/uploads/2018/06/2018-06-13T213137Z_1_LYNXMPEE5C26H_RTROPTP_4_SPACE-MARS-1019x573.jpg"
                    }
                    news_url={element.url}
                  />
                </div>
              );
            })}
          </div>
        </div>
        <div className="container d-flex justify-content-between">
          <Button
            variant="dark"
            disabled={this.state.page <= 1}
            onClick={this.handlePreviousClick}
          >
            &larr; Previous
          </Button>
          <Button
            disabled={
              this.state.page + 1 >
              Math.ceil(this.state.totalArticles / this.props.pagesize)
            }
            variant="dark"
            onClick={this.handleNextClick}
          >
            Next &rarr;
          </Button>
        </div>
      </div>
    );
  }
}

export default News;
